import { PropsWithChildren, ReactElement } from "react"
import { MaxWidthContainer } from "@rp/react/web/common/composite-components"
import { Box, ContainerProps } from "@rp/react/web/mui/core"

type DocumentViewerPageContainerProps = PropsWithChildren & {
  contentContainerProps?: ContainerProps
  documentViewer: () => ReactElement | null
  documentId: string
}

function DocumentViewerPageContainer({
  contentContainerProps,
  documentViewer,
  children,
  documentId,
}: DocumentViewerPageContainerProps) {
  return (
    <Box sx={{ width: "100%" }}>
      {documentId && documentViewer()}
      <MaxWidthContainer
        {...contentContainerProps}
        style={{
          ...contentContainerProps?.style,
          display: documentId ? "none" : contentContainerProps?.style?.display,
        }}
      >
        {children}
      </MaxWidthContainer>
    </Box>
  )
}

export { DocumentViewerPageContainer }
